.modal--overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  width: 100%;
  height: 100%;
  overflow: hidden;
  cursor: pointer;
}

.is-active .header__list {
  @media (max-width: $vp-767) {
    padding: 0;
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    font-family: $font;
  }
}

.is-active::before {
  content: "";
  position: absolute;
  background-image: url("../../img/svg/logo-menu.svg");
  background-position: no-repeat;
  background-size: 42px 22px;
  width: 42px;
  height: 22px;
}

.is-active .header__list a {
  color: $tangaroa;
}

.is-active {
  @media (max-width: $vp-767) {
    background-color: $alice-blue;
    padding-left: 25px;
    padding-top: 20px;
    padding-right: 15px;
    flex-direction: column-reverse;
    justify-content: flex-end;
    width: 95%;
    margin-left: auto;
    height: 100vh;
  }
}

.is-active .header__img {
  @media (max-width: $vp-767) {
    display: none;
  }
}

.is-active .header__button {
  margin-left: auto;
}

.is-active .header__button-close {
  @media (max-width: $vp-767) {
    display: block;
  }
}

.is-active .header__button-open {
  @media (max-width: $vp-767) {
    display: none;
  }
}
